import * as React from "react"
import './index.css';

export default function ContentBlock({ title, subtitle, subtitleAddition, children }) {
  return (
    <div className="content-block">
      <h4 className="content-block__title">
        {title}
      </h4>
      {(subtitle || subtitleAddition)
        ? <div className="content-block__subtitle">
          {subtitle && <div className="content-block__subtitle-main">{subtitle}</div>}
          {subtitleAddition && <div className="content-block__subtitle-addition">{subtitleAddition}</div>}
        </div>
        : null
      }
      <div className="content-block__content">
        {children}
      </div>
    </div>
  )
}
